/*
 * Login page
 * used for logging in
 * please refer to https://github.com/zeit/next.js/issues/153
 *
 */

//react components
import React, {Component} from 'react'
import Router from 'next/router'

//custom service
import AuthService from '../utils/authser.js'

import Layout from '../layouts/minimalist/border1.js'

//configure AuthService onto localhost:1996
const auth = new AuthService('http://localhost:1996')

class Logout extends Component {
	constructor(props) {
		super(props)
	}

	componentDidMount () {
		//logout the user
		auth.logout().then( res=> {
			console.log(res)

			//redirect to index page
			Router.push('/')
		}).catch( e=> {
			//error occurred
			if(e.response.status == 500){
				//back-end error, logout user from device anyways
				localStorage.removeItem('token');
				localStorage.removeItem('profile');
				//redirect back to login
				Router.push('/login')
			}

		})
	}

	render() {
		return (
		<div>
		<div>REDIRECTING....</div>
		</div>
		)
	}
}

export default Logout
